import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

export default function Info({ cardData, filteredCardData }) {
  return (
    <Box sx={{ display: { xs: "none", sm: "flex" }, alignItems: "center" }}>
      <Typography sx={{ pr: 2 }}>
        カード枚数:{filteredCardData.length}/{cardData.length}枚
      </Typography>
      <Typography>
        最終追加日:
        {new Date(cardData[cardData.length - 1].releaseAt).toLocaleDateString()}
      </Typography>{" "}
    </Box>
  );
}
